/* ui helper less rules */
@import "@{mockuplessPath}/base.less";
@import "@{mockupPath}/tooltip/pattern.tooltip.less";

/* loading icon animation */
.plone-loader {
    display: none;
    position: fixed;
    top: 50%;
    left: ~"calc(50% - 2.5em)";

    div {
        margin: -6em auto;
        font-size: 5px;
        text-indent: -9999em;
        border-top: 1.1em solid rgba(39, 33, 33, 0.2);
        border-right: 1.1em solid rgba(218, 211, 211, 0.2);
        border-bottom: 1.1em solid rgba(218, 211, 211, 0.2);
        border-left: 1.1em solid rgba(218, 211, 211, 0.2);
        -webkit-transform: translateZ(0);
        -ms-transform: translateZ(0);
        transform: translateZ(0);
        -webkit-animation: load8 1.1s infinite linear;
        animation: load8 1.1s infinite linear;
    }
    div,
    div:after {
        border-radius: 50%;
        width: 10em;
        height: 10em;
    }
    @-webkit-keyframes load8 {
        0% {
            -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
        }
        100% {
            -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
        }
    }
    @keyframes load8 {
        0% {
            -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
        }
        100% {
            -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
        }
    }
}
